:root {
  /* Fonts */
  --font-primary: "Poppins", sans-serif;
  /* Colors */
  --primary-color: #3caab4;
  --primary-color-100:#11AAB4;
  --secondary-color: #0074A2;
  --secondary-color-100: #285064;
  --dark-secondary-color: #102932;
  --extra-dark-secondary-color: #252A3B1C;
  --tertiary-color: #F08C3C;
  --tertiary-color-light: #F5B976;
  --gray-color: #8E8E8E;
  --gray-color-light: #FAFAFA;
  --charcoal-gray: #4A494B;
  --slate-gray: #6A6A6A;
  --body-color: #1B1B1B;
  --white-color: #FFFFFF;
  --white-color-100: #E8EBEF;
  --black-color: #000000;
  --red-color: #FF0000;
  /* Font Sizes */
  --font-size-62: 62px;
  --font-size-60: 60px;
  --font-size-56: 56px;
  --font-size-40: 40px;
  --font-size-30: 30px;
  --font-size-h4: 28px;
  --font-size-h3: 24px;
  --font-size-h1: 20px;
  --font-size-h2: 18px;
  --font-size-body: 16px;
  --font-size-15: 15px;
  --font-size-14: 14px;
  --font-size-0: 0;
  /* Font Weights */
  --font-weight-900: 900;
  --font-weight-700: 700;
  --font-weight-600: 600;
  --font-weight-500: 500;
  --font-weight-400: 400;
  /* Border Radius */
  --border-radius-extra-large: 16px;
  --border-radius-large: 8px;
  --border-radius: 4px;
  --border-radius-small: 2px;
  --border-radius-extra-small: 0;
}

.zwssgr-submit-btn {
  padding: 20px 24px;
  border-radius: var(--border-radius);
  border: 1px solid var(--secondary-color);
  background-color: var(--white-color);
  color: var(--secondary-color);
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  line-height: 1;
  cursor: pointer;
}
.zwssgr-submit-btn:hover {
  background-color: var(--secondary-color);
  color: var(--white-color);
}
.zwssgr-btn {
  background-color: var(--tertiary-color);
  padding: 20px 24px;
  border-radius: var(--border-radius);
  color: var(--dark-secondary-color);
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  border: 1px solid var(--tertiary-color-light);
  cursor: pointer;
}
.zwssgr-btn:hover {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  color: var(--white-color);
}
.zwssgr-select-btn {
  padding: 10px;
  font-size: var(--font-size-14);
}

.zwssgr-toogle-display {
  background-color: var(--gray-color-light);
  border: 1px solid var(--border-gray);
  padding: 30px;
  margin: 0 auto;
  text-align: center;
}
.zwssgr-option-item {
  display: none;
  margin: 20px 0;
  padding: 20px;
  background-color: var(--white-color);
  box-shadow: 0px 0.5px 2px 0px var(--extra-dark-secondary-color);
  border-radius: var(--border-radius-large);
}
.zwssgr-option-item.selected {
  border: 5px solid var(--secondary-color-100);
}
.zwssgr-option-item.selected[data-type=badge] {
  background-color: var(--primary-color);
}
.zwssgr-option-item.selected[data-type=badge] .zwssgr-layout-title {
  color: var(--black-color);
}

.zwssgr-radio {
  box-sizing: border-box;
}
.zwssgr-layout-radio {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: var(--white-color);
  box-shadow: 0px 0.5px 2px 0px var(--extra-dark-secondary-color);
  border-radius: var(--border-radius-extra-large);
  flex-wrap: wrap;
}
.zwssgr-layout-radio label {
  font-size: var(--font-size-15);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  line-height: 1;
  margin-right: 30px;
}
.zwssgr-nav-tab-wrapper {
  border-bottom: 1px solid rgb(221, 225, 231);
}
.zwssgr-nav-tab-wrapper .nav-tab {
  background: none;
  box-shadow: none;
  border: none;
  color: var(--secondary-color-100);
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-body);
  font-family: var(--font-primary);
  padding-bottom: 15px;
}
.zwssgr-nav-tab-wrapper .nav-tab-active {
  border-bottom: 3px solid var(--primary-color-100);
}
.zwssgr-tab-item {
  font-size: var(--font-size-14);
  font-weight: var(--font-weight-400);
  color: var(--charcoal-gray);
  font-family: var(--font-primary);
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  pointer-events: none;
  text-transform: capitalize;
  padding-bottom: 10px;
}
.zwssgr-tab-item .zwssgr-step {
  font-weight: var(--font-weight-600);
  color: var(--body-color);
  margin-right: 10px;
  position: relative;
  width: 18px;
  height: 19px;
  background: #f0f0f1;
  border-radius: 200%;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
}
.zwssgr-tab-item .zwssgr-step.done {
  font-size: var(--font-size-0);
}
.zwssgr-tab-item .zwssgr-step.done:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.done {
  pointer-events: all;
}
.zwssgr-tab-item.done .zwssgr-step {
  font-size: var(--font-size-0);
}
.zwssgr-tab-item.done .zwssgr-step:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.active {
  color: var(--secondary-color-100);
  font-weight: var(--font-weight-600);
  pointer-events: all;
}
.zwssgr-custom-tab {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 30px 0;
}
.zwssgr-custom-tab .zwssgr-step-arrow {
  position: relative;
  margin: 0px 20px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:before, .zwssgr-custom-tab .zwssgr-step-arrow:after {
  content: "";
  background: var(--charcoal-gray);
  position: relative;
  width: 10px;
  height: 2px;
  display: block;
  transform: rotate(45deg);
  border-radius: var(--border-radius-small);
  top: 6px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:after {
  transform: rotate(-45deg);
  top: 10px;
}
.zwssgr-tab-content-display {
